.alert-con {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 11;
}

.alert-context {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  z-index: 12;
  width: 8rem;
  height: 4.8rem;
  background-color: #fff;
  border-radius: 0.213333rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: stretch;
  align-items: stretch;
  -ms-flex-direction: column;
  flex-direction: column;
}

.alert-context .alert-content-detail {
  -ms-flex: 5 1;
  flex: 5 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 0.533333rem;
  color: #333;
  text-align: left;
}

.alert-context .confirm-btn {
  -ms-flex: 2 1;
  flex: 2 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 0.533333rem;
  color: #333;
  text-align: left;
  border-top: 1PX solid #eee;
}

.alert-enter {
  opacity: 0;
}

.alert-enter.alert-enter-active {
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
  opacity: 1;
}

.alert-leave {
  opacity: 1;
}

.alert-leave.alert-leave-active {
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
  opacity: 0;
}


