.spin-con {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 11;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 0.24rem;
  color: #975ec9;
}

.spin-enter {
  opacity: 1;
}

.spin-enter.spin-enter-active {
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
  opacity: 1;
}

.spin-leave {
  opacity: 1;
}

.spin-leave.spin-leave-active {
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
  opacity: 1;
}


