@import "../../assets/iconfonts/iconfont.css";

.header-container {
  background-color: #975ec9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 1.173333rem;
}

.header-container .header-slide-icon {
  font-size: 0.48rem;
  color: #fff;
  text-align: left;
  position: absolute;
  top: 50%;
  left: 0.4rem;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
}

.header-container .header-title {
  font-size: 0.533333rem;
  color: #fff;
  text-align: left;
}

.header-container .header-link {
  font-size: 0.533333rem;
  color: #fff;
  text-align: left;
  position: absolute;
  top: 50%;
  right: 0.4rem;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
}

.header-container .header-link-confim {
  font-size: 0.426667rem;
}

.header-container .nav-slide-list {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  top: 1.173333rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
}

.header-container .nav-slide-list .nav-link {
  font-size: 0.426667rem;
  color: #333;
  text-align: left;
  border-bottom: 1Px solid #ddd;
  margin: 0 0.4rem;
  padding: 0.466667rem 0.266667rem;
  position: relative;
}

.header-container .nav-slide-list .nav-link:before {
  position: absolute;
  right: 0.133333rem;
  top: 50%;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
  color: #666;
}

.header-container .nav-enter {
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
}

.header-container .nav-enter.nav-enter-active {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  -o-transition: transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}

.header-container .nav-leave {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.header-container .nav-leave.nav-leave-active {
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  -o-transition: transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}


