@import "../../assets/iconfonts/iconfont.css";

.navbar-container {
  background-color: #975ec9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 1.173333rem;
}

.navbar-container .navbar-slide-icon {
  font-size: 0.48rem;
  color: #fff;
  text-align: left;
  position: absolute;
  top: 50%;
  left: 0.4rem;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
}

.navbar-container .navbar-title {
  font-size: 0.533333rem;
  color: #fff;
  text-align: left;
}

.navbar-container .navbar-link {
  font-size: 0.533333rem;
  color: #fff;
  text-align: left;
  position: absolute;
  top: 50%;
  right: 0.4rem;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
}

.navbar-container .navbar-link-confim {
  font-size: 0.426667rem;
}


