.pdv-home .common-title {
  font-size: 0.48rem;
  color: #975ec9;
  text-align: left;
  padding: 0.4rem;
}

.pdv-home .home-form {
  background-color: #fff;
}

.pdv-home .home-form .home-form-tiem {
  height: 1.6rem;
  border-bottom: 1PX solid #eee;
}

.pdv-home .home-form .home-form-tiem span {
  font-size: 0.426667rem;
  color: #555;
  text-align: left;
  margin-left: 0.4rem;
  margin-right: 0.266667rem;
}

.pdv-home .home-form .home-form-tiem input {
  border: none;
  font-size: 0.4rem;
}

.pdv-home .home-form .home-form-tiem ::-webkit-input-placeholder {
  color: #ccc;
}

.pdv-home .common-select-btn {
  background-color: #fff;
