  align-items: center;
}

.pdv-home .common-select-btn .selected-pro-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.pdv-home .common-select-btn .selected-pro-list .selected-pro-item {
  width: 4.533333rem;
  margin: 0.133333rem;
  line-height: 0.666667rem;
  font-size: 0.426667rem;
  color: #333;
  text-align: center;
}

.pdv-home .upload-img-con {
  text-align: center;
}

.pdv-home .upload-img-con .file-lable {
