}

.pdv-home .upload-img-con .file-lable input[type='file'] {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: red;
  top: 0;
  right: 0;
  opacity: 0;
}

.pdv-home .upload-img-con .select-img {
  margin-top: 0.133333rem;
}

.pdv-home .submit-btn {
  width: 90%;
  background-color: #975ec9;
  margin: 0.666667rem auto 0;
  border-radius: 0.133333rem;
  line-height: 1.333333rem;
  font-size: 0.533333rem;
  color: #fff;
  text-align: center;
}


